<template>
<v-layout row justify-center>
    <v-dialog v-model="state" persistent max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Login</span>
          <v-spacer></v-spacer>
          <v-btn icon flat @click="close">
            <v-icon>
              highlight_off
            </v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <small>*indicates required field</small>
              <v-flex xs12 sm12 md12>
                <v-text-field
                type="text" 
                v-model="login.username"
                label="Legal first name *" 
                required>
                </v-text-field>
              </v-flex>
              <v-flex xs12 sm12 md12>
                <v-text-field 
                type="password"
                v-model="login.password"
                label="Legal middle name *" 
                required>
                </v-text-field>
              </v-flex>
              <v-flex xs12 sm12 md12 v-if="loading">
                <v-btn block color="secondary" light disabled>Login</v-btn>
              </v-flex>
              <v-flex xs12 sm12 md12 v-else-if="validate">
                <v-btn block color="secondary" light disabled>Login</v-btn>
              </v-flex>
              <v-flex xs12 sm12 md12 v-else>
                <v-btn block color="secondary" dark @click="handleLoginFormSubmit">Login</v-btn>
              </v-flex>
              <v-flex xs12 sm12 md12>
                Don't have an account? 
              <router-link
                  to="/register" 
                  class="links">
                  Sign up
              </router-link>
              Here
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import {basUrl, getHeader, userurl} from '../conf.js'

  export default {
    props: {
      state: null,
    },
    data () {
      return {
          login: {
              client_id : "ads5x1fe2m",
              client_secret : "ads5x1fe2m",
              username: '+923331424188',
              password: 'SahamNadeem1'
            },
          loading : false,
        }
    },
    computer:{
    },
    methods: {
      close: function () {
        this.$store.dispatch('changestate')
      },
      handleLoginFormSubmit: function () {
                this.loading = true
                setTimeout(()=>{
                    const login = {
                    username : this.login.username,
                    password : this.login.password,
                    client_id : this.login.client_id,
                    client_secret : this.login.client_secret
                }
                console.log(login)
                const authUser = {}
                
                this.$http.post(basUrl+"/oauth/",login)
                .then(responce => {
                    if(responce.status === 200){
                        authUser.access_token = responce.data.access_token
                        window.localStorage.setItem('authUser',JSON.stringify(authUser))
                        this.$http.get(basUrl+"/oauth/user/",{headers: getHeader()})
                        .then(responce =>{
                            authUser.email = responce.data.email
                            authUser.username = responce.data.username
                            authUser.first_name = responce.data.first_name
                            authUser.last_name = responce.data.last_name
                            window.localStorage.setItem('authUser',JSON.stringify(authUser))
                            this.$store.dispatch('SetUserObject', authUser)
                            this.$router.push({name: 'dashboard'})
                            this.$store.dispatch('changestate')
                            this.loading = false
                            location.reload()
                        })
                    }
                    if(responce.status === 400){
                        console.log('Error occured while loggin in')
                    }
                })
            },3000)
        },
    },
    computed: {
      validate: function () {
        if (this.login.username === '' || this.login.password === '') {
          return true
        }else{
          return false
        }
      },
      auth: function () {
        return this.$store.getters.getauth
      }
    }
  };
</script>

<style scoped>
.links{
  text-decoration: none;
  color: blue;
  font-weight: italic;
}
</style>