<template>
<v-app>
  <v-toolbar fixed light color="primary">
    <v-toolbar-side-icon class="hidden-md-and-up" @click.stop="drawer = !drawer"></v-toolbar-side-icon>
    <v-spacer class="hidden-md-and-up"></v-spacer>
    <v-toolbar-title><router-link to="/" class="links grey--text font">Nascents<img src="../assets/nfc-icon-16.png" class="logo"></router-link></v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items class="color">
    	<v-divider vertical class="hidden-sm-and-down color"></v-divider>
       	<v-btn icon class="hidden-md-and-up">
       		<v-icon color="grey">search</v-icon>
       	</v-btn>
       	<v-btn flat class="hidden-sm-and-down">
	    <v-flex sm12 v-if="show">
          <v-text-field
          color="grey"
            value=""
            class="searchbar"
            v-on:blur="clear"
            autofocus
          ></v-text-field>
        </v-flex>
        <v-icon v-else color="grey" @click="showchange">search</v-icon>
	    </v-btn>
	    <v-menu 
	    :nudge-width="100" 
	    v-if="getuser === false">
        <v-toolbar-title slot="activator">
          <span><v-icon>person</v-icon></span>
          <v-icon dark>arrow_drop_down</v-icon>
        </v-toolbar-title>
        <v-list>
          <v-list-tile @click="HandleLogout">
            <v-list-tile-title>Logout</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      	<v-divider vertical class="hidden-sm-and-down color"></v-divider>
     	<v-btn flat class="hidden-sm-and-down color">Sign up</v-btn>
     	
    </v-toolbar-items>
  </v-toolbar>
  <v-navigation-drawer
	      v-model="drawer"
	      absolute
	      temporary>

	      <div v-if="getuser === false">
	      <v-list class="pa-1">
	        <v-list-tile avatar>
	          <v-list-tile-avatar>
	            <img src="https://randomuser.me/api/portraits/men/85.jpg">
	          </v-list-tile-avatar>
	          <v-list-tile-content>
	            <v-list-tile-title>John Leider</v-list-tile-title>
	          </v-list-tile-content>
	        </v-list-tile>
	      </v-list>

	      <v-list class="pt-1" dense>
	        <v-divider></v-divider>
	        <v-list-tile
	          @click="HandleLogout">
	          <v-list-tile-action>
	            <v-icon>lock-open</v-icon>
	          </v-list-tile-action>
	          <v-list-tile-content>
	            <v-list-tile-title>Logout</v-list-tile-title>
	          </v-list-tile-content>
	        </v-list-tile>
	      </v-list>
	      </div>

	      <div v-else>
	      	<v-list class="pt-0" dense>
		        <v-divider></v-divider>
		        <v-list-tile
		          v-for="item in items"
		          :key="item.title"
		          @click="">
		          <v-list-tile-action>
		            <v-icon>{{ item.icon }}</v-icon>
		          </v-list-tile-action>
		          <v-list-tile-content>
		            <v-list-tile-title><router-link :to="item.link" class="links grey--text"> {{ item.title }} </router-link></v-list-tile-title>
		          </v-list-tile-content>
		        </v-list-tile>
		      </v-list>
	      </div>
	    </v-navigation-drawer>
</v-app>
</template>

<script type="text/javascript">
	import {mapState} from 'vuex'
	
	export default{
		name: 'Navbar',
		data () {
			return {
				show:false,
				drawer: false,
				items: [
		          { title: 'Home', icon: 'dashboard', link:"/register" },
		          { title: 'About', icon: 'question_answer', link:"/about" }
		        ]
			}
		},
		 computed: {
		 	getuser: function () {
		 		if (this.$store.getters.getauth !== null) {
		 			return true
		 		}else{
		 			return false
		 		}
		 	}
	    },
	    methods: {
	    	HandleLogout : function(){
	    		if ( this.$store.getters.showstate === false) {
	    			this.$store.dispatch('stateregister')
	    		}
                window.localStorage.removeItem('authUser')
                this.$router.push({name:'login'})
                this.$store.dispatch('ClearUserObject')
	    	},
	    	showchange: function () {
	    		this.show = true
	    	},
	    	clear: function () {
	    		this.show = false
	    	}
	    }
	};
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Cinzel+Decorative|Sacramento');
.font{
	font-family: 'Cinzel Decorative', cursive;
	font-weight: bold;
}
.color{
	color: #828282;
}
.v-divider--vertical{
	margin-top: -1;
}
.searchbar{
	font-weight: normal;
}
.logo{
	width: 50px;
}
.links{
	text-decoration: none;
	color: 
}
</style>