<template>
	<div class="row">
    	<div class="col-12 offset-0 col-md-3 col-sm-12 col-lg-3">
            <gmap-autocomplete
            placeholder="Search location.."
                class="search"
                v-model="valueof"
                @place_changed ="setPlace"
                v-on:keyup.13 = "addMarker">
            </gmap-autocomplete>
    	</div>
    	<div class="col-12 col-md-3 col-sm-12 col-xs-12 col-xl-3">
    		<select class="search">
    			<option value="" selected="">Select Catagory</option>
    			<option v-for="item in catagoryList" :key="item.id" value="item.id">{{item.lable}}</option>
    		</select>
    	</div>
    	<div class="col-12 col-md-3 col-sm-12 col-xs-12 col-xl-3">
    		<p style="margin-top:10px;">Radious {{slider}}</p>
    		<v-icon style="display:inline-block; color:green;" @click='decriment'>
              exposure_neg_1
            </v-icon>
            &nbsp;
            &nbsp;
    		<v-slider 
		        v-model="slider" color="secondary" style="display:inline-block;">
		    </v-slider>
		    &nbsp;
		     <v-icon style="display:inline-block; color:green;" @click='incriment'>
              exposure_plus_1
            </v-icon>
    	</div>
    	<div class="col-12 col-md-3 col-sm-12 col-xs-12 col-xl-3">
    		<button class="btn btn-success search" type="button">Search</button>
    	</div>
    </div>
</template>
<script>
	export default {
		data () {
			return {
				slider:0,
                valueof:'',
                latlng:{
                    lat:0,
                    lng:0
                }
			}
		},
		methods: {
			incriment: function(){
				this.slider=this.slider+1;
			},
			decriment: function(){
				if (this.slider >0) {
					this.slider=this.slider-1;
				}
			},
            setPlace(place){
                this.latlng = {
                    lat: place.geometry.location.lat(),
                    lng: place.geometry.location.lng()
                }
                this.$store.dispatch("updateLocation", this.latlng)
            },
            locationget: function(){
                if (navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition((position)=>{
                      this.latlng.lat = position.coords.latitude
                      this.latlng.lng = position.coords.longitude
                    });
                    this.$store.dispatch("updateLocation", this.latlng)
                }
            }
		},
		computed: {
            catagoryList: function(){
                return this.$store.getters.getCatagory
            }

		},
        mounted(){
            this.locationget();
        }
	};
</script>

