<script>
import login from '@/components/login.vue'

export default {
  components: {
    login,
  },
  data () {
    return {
      scrolled: true,
      dialog:false,
      show: false,
      sideNave: false,
      offsetTop: null,
      img: 'https://randomuser.me/api/portraits/men/85.jpg',
      items: [
        { title: 'Login', icon: 'lock_open', method:'open', link:null },
        { title: 'Home', icon: 'home', link:'/' }
      ]
    }
  },
  methods: {
    HandleLogout : function(){
      window.localStorage.removeItem('authUser')
      this.$store.dispatch('ClearUserObject')
      this.sideNave = false
      location.reload()
    },
    showchange: function () {
      this.show = true
    },
    clear: function () {
      this.show = false
    },
    open: function () {
       if (this.sideNave === true) {
      this.sideNave = false
    }
      this.$store.dispatch('changestate')
    },
    scrollFunction: function (e) {
      //this.offsetTop = window.pageYOffset || document.documentElement.scrollTop
      // if (this.offsetTop >= 10) {
      //   this.flat = true
      // }
    },
    handleScroll () {
      if (this.scrolled === true) {
        this.scrolled = window.scrollY < 0;
      }else if(window.scrollY === 0){
        this.scrolled = true
      }
    }
  },
  computed: {
    getuser: function () {
      if (this.$store.getters.getauth === null) {
          return false
      }else{
          return true
      }
    },
    opend: function () {
        return this.$store.getters.showstate
    }
  },
  created() {
    const authUser = JSON.parse(window.localStorage.getItem('authUser'))
    this.$store.dispatch('SetUserObject', authUser)
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeMount () {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  mounted() {

  }
};
</script>

