import {basUrl, getHeader, userurl} from '../../conf.js'
import axios from 'axios'

const state = {
	authUser: '',
	users : '',
	register : '',
	show : false,
	profile : '',
	images : [],
	location:{
		lat:0,
		lng:0
	},
	cats:null,
}

const mutations = {
	SET_AUTH_USER (state, userObj){
		state.authUser = userObj
	},
	CLEAR_AUTH_USER (state){
		state.authUser = null
	},
	getusers (state, data){
		state.users = data
	},
	ChangeState (state, data){
		state.show = !state.show
	},
	SET_IMAGE (state, data){
		console.log(state.images)
		state.images.push(data)
	},
	DEL_IMAGE (state, index) {
		for (let i = state.images.length - 1; i >= 0; --i) {
		    if (state.images[i].id == index) {
		        state.images.splice(i,1);
		    }
		}
	},
	UPDATE_LOCATION(state, location) {
		state.location = location
	},
	LOADCATS(state, cats){
		state.cats = cats
	}
}

const actions = {
	SetUserObject: ({commit}, userObj) => {
		commit('SET_AUTH_USER', userObj)
	},
	ClearUserObject: ({commit}) => {
		commit('CLEAR_AUTH_USER')
	},
	getusers(context) {
		axios.get(basUrl+'/users/', {
			headers: getHeader()
		})
		.then(response => {
			context.commit('getusers', response.data)
		})
		.catch(error => {
			console.log(error)
		})
	},
	changestate(context) {
		context.commit('ChangeState')
	},
	register (context, payload){
		const authUser = {}
		axios.post(basUrl+'/oauth/register/', payload)
		.then(response => {
			authUser.access_token = response.data.access_token
            window.localStorage.setItem('authUser',JSON.stringify(authUser))
            axios.get(basUrl+"/oauth/user/",{headers: getHeader()})
            .then(responce =>{
                authUser.email = responce.data.email
                authUser.username = responce.data.username
                authUser.first_name = responce.data.first_name
                authUser.last_name = responce.data.last_name
                window.localStorage.setItem('authUser',JSON.stringify(authUser))
                context.commit('SET_AUTH_USER', authUser)
                context.commit('ChangeState')
            })
            .catch(error =>{
            	console.log(error)
            })
		})
		.catch(error => {
			console.log(error)
		})
	},
	setImage (context, payload) {
		context.commit('SET_IMAGE', payload)
	},
	deleteImg (context, payload) {
		context.commit('DEL_IMAGE', payload)
	},
	updateLocation(context, payload){
		context.commit('UPDATE_LOCATION', payload)
	},
	loadCats(context){
		axios.get(basUrl+"catagory/")
		.then(response =>{
			console.log("data aa gya")
			context.commit('LOADCATS', response.data)
		})
		.catch(error=>{
			console.log(error)
		})
	}
}

const getters = {
	list (state) {
		return state.users
	},
	showstate (state) {
		return state.show
	},
	getauth(state) {
		return state.authUser
	},
	getImages(state) {
		return state.images
	},
	getLocation(state){
		return state.location
	},
	getCatagory(state){
		return state.cats
	}
}

export default {
	state, mutations, actions, getters
}