<template>
    <div class="row">
        <img 
        :src="image" 
        class="img-responsive img" 
        v-if="uploading" 
        v-on:click="deleted">
        <span 
        class="mybtn btn-file" 
        v-else>
            <i class="fa fa-plus" style="font-size:24px; color:#dbdbdb; margin:auto;">
                <input type="file" v-on:change="onFileChange">
            </i>
        </span>
    </div>
</template>
<style scoped>
    img{
        max-height: 36px;
    }
</style>
<script>
import {basUrl, getHeader, userurl} from '../conf.js'

    export default{
        props:{
            id:Number,
        },
        data(){
            return {
                image: '',
                uploading: null
            }
        },
        methods: {
            onFileChange(e) {
                let files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.uploading = e.target.files[0]
                const fd = new FormData();
                fd.append('file', this.uploading, this.uploading.name)
                axios.post('http://localhost:8056/ceptraproj/img/upload/', fd, {
                    headers: getHeader()
                })
                .then(response => {
                    this.createImage(files[0]);
                    const obj = {
                        id: this.id,
                        data: response.data[0]
                    }
                    this.$store.dispatch('setImage', obj)
                });
            },
            createImage(file) {
                let reader = new FileReader();
                let vm = this;
                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file);
            },
            deleted: function () {
                this.uploading = null
                this.image = null
                this.$store.dispatch('deleteImg', this.id)

            }
        }
    };
</script>

<style scoped>
    .btn-file {
        position: relative;
        overflow: hidden;
    }
    .btn-file input[type=file] {
      position: absolute;
      top: 0;
      right: 0;
      min-width: 100%;
      min-height: 100%;
      font-size: 100px;
      text-align: right;
      filter: alpha(opacity=0);
      opacity: 0;
      outline: none;
      background: red;
      cursor: inherit;
      display: block;
    }
    .mybtn{
        padding: 20px;
        border: 2px solid #dbdbdb;
        border-style: dashed;
        background-color:;
    }
    .img{
        
        max-height: 80px;
        max-width: 100px;
    }
</style>